const regularTest = {
    testNum(val){
        //
        return (/^\d+$/g).test(val);
    },
    testName(val){
        //
        return (/^[\u4e00-\u9fa5\s\.-]{1,25}$|^[\@A-Za-z_\s\.-]{1,50}$|^[A-Za-z\u4e00-\u9fa5]{1,20}$|^[\u4e00-\u9fa5A-Za-z]{1,20}$/).test(val);
    },
    testPhoneNum(val){
        // 1617
        //return (/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/).test(val);
        return (/^1\d{10}$/).test(val);
    },
    testID(value){
        //
            let city = {
                11: "",
                12: "",
                13: "",
                14: "",
                15: "",
                21: "",
                22: "",
                23: " ",
                31: "",
                32: "",
                33: "",
                34: "",
                35: "",
                36: "",
                37: "",
                41: "",
                42: " ",
                43: "",
                44: "",
                45: "",
                46: "",
                50: "",
                51: "",
                52: "",
                53: "",
                54: " ",
                61: "",
                62: "",
                63: "",
                64: "",
                65: "",
                71: "",
                81: "",
                82: "",
                91: " "
            };
            let tip = "",
                pass = true;
            value = value.toUpperCase();
            if (value.length == 15) {
                //15
                pass = false;
            } else if (!value || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(value)) {
                //tip = "";
                pass = false;
            } else if (!city[value.substr(0, 2)]) {
                //tip = "";
                pass = false;
            } else if (value.length == 18) {
                //18
                value = value.split('');
                //(aiWi)(mod 11)
                //
                let factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2],
                    parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2],//
                    sum = 0,
                    ai = 0,
                    wi = 0;
                for (let i = 0; i < 17; i++) {
                    ai = value[i];
                    wi = factor[i];
                    sum += ai * wi;
                }
                if (parity[sum % 11] != value[17]) {
                    //tip = "";
                    pass = false;
                }
            }
            return pass;
    },
    isPInt(str) {
        return (/^[1-9]*[1-9][0-9]*$/).test(str);
    },
    decimals(str){
        return (/^\d+(\.\d+)?$/).test(str);
    },
    minus(str){
        return (/^(\-|\+)?\d+(\.\d+)?$/).test(str);
    }
};
export default  regularTest;
