class dateData {
    //
    constructor(){
        this.yearTimeBox=[];
        this.monthTimeBox=[];
        this.dayTimeBox=[];
        this.getYearDate();
    }
    getYearDate(){
        let t = this;
        const dateTime = new Date();
        for(let num = dateTime.getFullYear();num>1899;num--){
            t.yearTimeBox.push(num);
        }
        t.getMonthDate();

    }
    getMonthDate(){
        let t = this;
        for(let num = 1;num<12+1;num++){
            t.monthTimeBox.push(num);
        }
        const dateTime = new Date();
        t.getDayDate(dateTime.getFullYear(),dateTime.getMonth()+1);
    }
    getDayDate(y,m){
        let t = this;
        const year=y; //
        const month=m;//
        this.dayTimeBox=[];
        const curMonthDays=new Date(year,month,0).getDate(); //030,0
        ////console.log(''+curMonthDays+"");
        for(let num = 1;num<curMonthDays+1;num++){
            t.dayTimeBox.push(num);
        }
        ////console.log(t.yearTimeBox,t.monthTimeBox,t.dayTimeBox);
        return t.dayTimeBox;

    }
}
export default  dateData;
